<template>
  <div class="edit-form mt-5">
        
      <h1 class="d-flex justify-content-center"> {{ itemx.nombre }} </h1>         

      <div class="d-flex justify-content-between">
        <div class="mt-5">
          <h5>Precio Unitario</h5>
        </div>

        <div class="mt-5">
          <h5>Precio Total</h5>
        </div>  
      </div>


      <div class="d-flex justify-content-between">
        <div class="d-flex my-4 mx-5">
          <h3>${{ itemx.precio }}</h3>
        </div>

        <div class="d-flex my-4 mx-5">
          <h3>${{ precioTotal }}</h3>
        </div>
      </div>  

      <div class="d-flex justify-content-between align-items-center mt-5">
        <h4 class="mx-4">Cantidad</h4>
        <div class="btn-toolbar d-flex" role="toolbar">



            <input class="w-75" v-model="cant" type="number" placeholder="Ingresar cantidad" > 



        </div>
      </div>      


      <div class="d-flex justify-content-center mt-5">
        <button @click="agregarItem" class="btn btn-success w-100"
        >
        Confirmar
        </button>
      </div>
  </div>

</template>

<script>
import { reactive, computed, onMounted} from "vue"
import { useRoute } from "vue-router"
import { getItem } from "@/firebase"
import { useStore } from "vuex"

export default {
  setup() {
    const store = useStore()
    const route = useRoute()
    const itemId = computed(() => route.params.id)

    const itemx = reactive({ nombre: "", precio: undefined})
    onMounted(async () => {
      const item = await getItem(itemId.value)
      itemx.nombre = item.nombre
      itemx.precio = item.precio
    })

    return { itemx, store, itemId }

  },
  data:() => {
    return {
      cant: null,
    }
  },
  methods: {
    agregar() {
      this.cant = this.cant + 1;
    },
    restar() {
      this.cant = this.cant - 1;
    },
    agregarItem() {
      this.store.commit("agregarItem", ({
        id: this.itemId,
        nombre: this.itemx.nombre,
        cantidad: this.cant,
        precioU: this.itemx.precio,
        precioT: this.itemx.precio * this.cant
      }))
      this.$router.push("/")
    }
  },
  computed: {
      precioTotal() {
        return this.itemx.precio * this.cant;
      },
  }
}

</script>

<style>
.edit-form {
  max-width: 300px;
  margin: auto;
}
</style>